.top {
    background: $gradient_soft;
    border-radius: 0 0 12px 12px;
    margin-bottom: 35px;
    position: relative;
    &::before,
    &::after {
        content: "";
        position: absolute;
        left: calc(20px - 0%);
        right: calc(20px - 0%);
        height: 10px;
        border-radius: 0 0 12px 12px;
    }
    &::before {
        background-color: #e7e7f2;
        bottom: -20px;
        left: calc(40px - 0%);
        right: calc(40px - 0%);
    }
    &::after {
        background-color: #cacadb;
        bottom: -10px;
    }
    &__bg {
        position: relative;
        &::before {
            content: "";
            position: absolute;
            width: 431px;
            height: 501px;
            background: url("../img/icons/bg_mobile.svg") 0 0 no-repeat;
            left: 50%;
            top: 50%;
            transform: translate(-66%, -23%);
        }
    }
}

@media (min-width: 992px) {
    .top {
        margin-bottom: 0;
    }
}

.top--all-serv {
    background: $gradient_supersoft;
}
